<template>
  <main class="feed" v-on:scroll="scroll">
  	<div class="header">
  		<div class="title">{{ title }}</div>
  		<ul class="categories">
  				<li v-on:click="category = 500770">Nature</li>
  				<li v-on:click="category = 1192182">London</li>
  				<li v-on:click="category = 1120384">Street</li>
  				<li v-on:click="category = 1086794">Mountains</li>
  				<li v-on:click="category = 534122">Sun</li>
  		</ul>
  	</div>

  	<div class="feed__grid">
      <div class="feed__grid-item" v-for="photo in photos">
          <img :src="photo.urls.small" alt="">
          <a :href="photo.links.html"><div class="hover" v-bind:style="{background: photo.color}"></div></a>
      </div>
	</div>
		<!-- load spin -->
    <div class="loading" v-if="loadState === 1">loading ...</div>
   	<div class="error" v-else-if="loadState === 2"> error ...</div>

    
  </main>
</template>
<!-- 1192182,
1120384,
534122
1086794
500770
 -->
<script>
	import axios from 'axios'
	export default {
	name: 'Feed',
    data ()  {
    	return {
        title: 'Works',
        photos: [],
        category: 500770,
    	totalPhotos: 0,
    	perPage: 12,
    	currentPage: 1,
    	allowToFetch: false,
    	loadState: 0 // 0-load,1-loading,2-error
    	}
    },
    mounted ()  {
    	this.loadState = 1
    	this.getPosts(this.currentPage,0)
    },
    watch: {
    	category() {
    		console.log(this.category)
    		this.getPosts(1,1)
    	}
    },  	
    methods: {
    	getPosts (page,method) {
    		// method 0 - simple, 1 - new category
    		let options = {
        		params: {
          			client_id: '1504bf5877fd6ef4bb4bd3cb83cc9fdc89258ee0ec9456555f90b083fc065a44',
          			id: this.category,
          			page: page,
          			per_page: this.perPage
        		}
      		}
      		axios.get(`https://api.unsplash.com/collections/${options.params.id}/photos`, options)
  			.then(response => {
  				console.log(response.data)
    			this.photos = (method == 0)? this.photos.concat(response.data) : response.data
        		this.totalPhotos = parseInt(response.headers['x-total'])
        		this.currentPage = page
        		this.allowToFetch = true
        		this.loadState = 0
  			})
  			.catch(error => {
    			this.loadState = 2
  			});
    	},
    	scroll (event) {
    		let feedWrapper = event.target
    		let feed = feedWrapper.children[1]
    		
    		// console.log(feedWrapper.children[1])

    		// getPost 
    		if (feed.offsetHeight - feedWrapper.offsetHeight <= feedWrapper.scrollTop&this.allowToFetch == true) {

    			// check pages
    			if ((this.currentPage)*12 <= this.totalPhotos){
    				// get photos
    				this.allowToFetch = false
    				this.loadState = 1
					this.getPosts(this.currentPage + 1,0)
    			} else { return }
    			

			}
    	},
    },
  }
</script>
<style lang="scss" src="./style.scss" scoped></style>


